import React from 'react';
import ImageSection from './ImageSection';
import SkillsSection from './SkillsSection';
import Title from './Title';

function About() {
    return (
        <div className="about">
            <Title title={'About Me'} span={'About Me'} />
            <ImageSection />
            <Title title={'My Skills'} span={'My Skills'} />
            <div className="skillsContainer">
                <SkillsSection skill={'HTML'} progress={'80%'} width={'80%'} />
                <SkillsSection skill={'CSS'} progress={'70%'} width={'70%'} />
                <SkillsSection skill={'JAVASCRIPT'} progress={'70%'} width={'70%'} />
                <SkillsSection skill={'REACT JS'} progress={'70%'} width={'70%'} />
                <SkillsSection skill={'PYTHON'} progress={'90%'} width={'90%'} />
                <SkillsSection skill={'JAVA'} progress={'60%'} width={'60%'} />
                <SkillsSection skill={'C/C++'} progress={'60%'} width={'90%'} />
            </div>
        </div>
    )
}

export default About
