import React from 'react'
import Title from './Title';
import blind from '../img/blind.jpg';
import bot from '../img/bot.jpg';
import face from '../img/face.jpg';
import ProjectsSection from '../components/ProjectsSection';
function Projects({image,title,text}) {
    return (
        <div className = "project-section">
            <div className = "project-title">
                <Title title={'MY PROJECTS'} span={'MY PROJECTS'} />
            </div>
            <div className="projects-container">
                <ProjectsSection image = {blind}  title={'VOICE BASED EMAIL'} link = {'https://github.com/roshan13ghimire/Voice_Based_Email'}
                />
                <ProjectsSection image = {bot}  title={'CHAT BOT STOCK'} link = {'https://github.com/roshan13ghimire/ChatBot'}
                />
                <ProjectsSection image = {face}  title={'SMART ATTANDANCE '} link = {'https://github.com/roshan13ghimire/Internship_Projects'}
                />
            </div>
            
        </div>
    )
}

export default Projects;
