import React from 'react'
import { faFacebook } from '@fortawesome/free-brands-svg-icons';
import { faGithub } from '@fortawesome/free-brands-svg-icons';
import { faLinkedin } from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

function Home() {
    return (
        <div className="home">
            <header className="hero">
                <h1 className="hero-text">
                    Hi,I am <span>Roshan Ghimire.</span>
                </h1>
                <p className="subtext">
                    I have a good knowledge in programming language specially in python, And I have intrest in Web Development also. I know Html,Css and Js.
                </p>
                <div className="icons">
                    <a href="https://www.facebook.com/roshan13ghimire" target="roshan" className="icon-holder">
                        <FontAwesomeIcon icon={faFacebook} className="icon fb" />
                    </a>
                    <a href="https://www.github.com/roshan13ghimire" target="roshan" className="icon-holder">
                        <FontAwesomeIcon icon={faGithub} className="icon gh" />
                    </a>
                    <a href="https://www.linkedin.com/in/roshan-ghimire-b22b971a0/" target="roshan" className="icon-holder">
                        <FontAwesomeIcon icon={faLinkedin} className="icon ld" />
                    </a>
                </div>
            </header>
        </div>
    )
}

export default Home
