import React from 'react';
import Title from './Title';
import ServicesSection from '../components/ServicesSection';
import design from '../img/design.svg';
import intelligence from '../img/intelligence.svg';
import gamedev from '../img/game-dev.svg';

function Services({ image, title, text }) {
    return (
        <div className="ServicesSection">
            <Title title={'My SERVICES'} span={'My SERVICES'} />
            <div className="servives-container">
                <ServicesSection image={design} title={'Web Design'}
                    text={'I can work on Front end as well as Backend also.'}
                />
                <ServicesSection image={intelligence} title={'Python Web Scraping'}
                    text={'I can extract the datas from the websites.'}
                />
                <ServicesSection image={gamedev} title={'App Developement'}
                    text={'I can work on App and Game Development also.'}
                />


            </div>
        </div>
    )
}

export default Services;



